import Vue from 'vue'

export default {
    namespaced: true,
    state: {
        quote: {
            text: null,
            author: false,
            fetchedAt: null
        }
    },

    getters: {
        quote(state) {
            return state.focus;
        }
    },

    mutations: {
        setQuote(state, quote) {
            Vue.set(state, 'quote', quote);
        }
    }
}